import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';

import { ActionSheetController, AlertController, Platform } from '@ionic/angular';
import { PhotoService } from 'src/app/services/photo.service';
import { ApiService } from 'src/app/services/api.service';
import { DomSanitizer } from '@angular/platform-browser';
import { CommonserviceService } from 'src/app/services/commonservice.service';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { DataService } from 'src/app/services/data.service';
import { ToastService } from 'src/app/services/toast.service';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { File, FileEntry } from '@ionic-native/file/ngx';
import { FilePath } from '@ionic-native/file-path/ngx';
import { Crop } from '@ionic-native/crop/ngx';
import { Router } from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProfilePage implements OnInit {
  @ViewChild('file') fileInput: ElementRef;
  flag: boolean;
  currentImage: any;
  clickedImage: string;
  phoneNumber: string;
  fullName: any;
  email: any;
  profileData: any;
  image: any;
  formData: any;
  selectedFile: File;
  progress: number;
  customerId: any;
  showLoader: boolean;
  currentUser: any;
  actionSheet: any;

  constructor(private api: ApiService, public platform: Platform,
    public actionSheetController: ActionSheetController,
    private sanitizer: DomSanitizer,
    private cdr: ChangeDetectorRef,
    private filePath: FilePath,
    private camera: Camera,
    private router: Router,
    private photoService: PhotoService,
    private commonService: CommonserviceService,
    public dataService: DataService,
    private alert: AlertController,
    private file: File,
    private crop: Crop,
    private toastService: ToastService) { }

  ngOnInit() {
    this.currentUser = this.dataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');

    this.api.custpomerDetails(this.phoneNumber).subscribe((resp: any) => {
      this.formData = resp;
      this.commonService.sendProfileInfo(resp);
      this.customerId = resp.customerId;
      this.assign(resp.firstName, resp.middleName, resp.lastName, resp.primaryEmailAdress);
      this.getProfilePicture(resp.customerId);
    });

    this.dataService.getAvatarUrl.subscribe((data) => {
      if (data != null) {
        let objectURL = data;
        this.image = this.sanitizer.bypassSecurityTrustUrl(objectURL);
        this.cdr.detectChanges();
      }
    });

  }

  assign(firstName: any, middleName: any, lastName: any, email) {
    if (middleName == null) {
      this.fullName = firstName + ' ' + lastName;
    }
    else {
      this.fullName = firstName + ' ' + middleName + ' ' + lastName;
      console.log("full name", this.fullName)
    }
    this.email = email;
  }

  settings() {
    this.router.navigate(['settings']);
  }
  changePassword() {
    this.router.navigate(['changepassword']);
  }
  securityCenter() {
    this.router.navigate(['securitycenter']);
  }
  account() {
    this.router.navigate(['account']);
  }
  notification() {
    this.router.navigate(['notification']);
  }
  menu() {
    this.flag = true;
  }
  getProfilePicture(customerId) {
    this.api.getProfileDetails(customerId)
      .subscribe((data: any) => {
        console.log('get profile data -- ', data);
        this.profileData = data;
        if (data.profileImage != null) {
          let objectURL = data.profileImage;
          this.image = data.profileImage != "not_available" ? this.sanitizer.bypassSecurityTrustUrl(objectURL) : undefined;
        } else {
          this.image = null;
        }
        this.cdr.markForCheck();
      }, (error: any) => {
        console.log(error);
      });
  }

  cancel() {
    this.flag = false;

  }
  addPhotoToGallery() {
    this.photoService.addNewToGallery();
  }


  async openActionSheet() {
    this.actionSheet = await this.actionSheetController.create({
      // header: 'Albums',
      // subHeader: 'Subtitle',
      animated: false,
      backdropDismiss: false,
      cssClass: 'custom_action_css',
      mode: 'ios',
      buttons: [{
        text: 'Take Photo',
        // icon: 'share',
        handler: () => {
          this.pickImage(this.camera.PictureSourceType.CAMERA);
        }
      }, {
        text: 'Upload Photo',
        handler: () => {
          this.fileInput.nativeElement.click();
        }
      }, {
        text: 'Remove Photo',
        role: 'destructive',
        handler: () => {
          this.removeProfileImage();
          this.image = null;
        }
      }, {
        text: 'Cancel',
        role: 'cancel',
        handler: () => {
        }
      }]
    });
    await this.actionSheet.present();
  }
  goToHelp() {
    this.router.navigate(['help']);
  }
  goToFaq() {
    this.router.navigate(['faq']);
  }

  goToWallet() {
    this.router.navigate(['/wallet']);
  }

  handleProfileUpdate(event) {
    const file = event.target.files[0];
    if(file) {
      const formData: FormData = new FormData();
      let payload: any = {
        "documentName": "Profile",
        "documentType": 1,
        "fileType": file?.type,
        "fileName": file?.name,
        "customerId": this.customerId
      };
      formData.append('file', file);
      formData.append('data', JSON.stringify(payload));
      this.actionSheet?.dismiss();
  
      this.api.uploadProfileImage(formData).subscribe(resp => {
        console.log(resp);
        this.image = resp.fileUrl;
        localStorage.setItem('profilePicInfo', JSON.stringify(resp));
      }, err => console.log('Error: ', err));
    }
  }

  removeProfileImage() {
    const data = {
      custId: this.customerId,
      documentType: 1,
    }
    this.api.deleteProfileImage(data).subscribe((resp: any) => {
      console.log('res: ', resp);
    }, (err: any) => {
      console.log('Error: ', err.status);
      if (err?.status == 200) {
        this.image = null;
        this.toastService.showToast('Profile removed Successfully!');
      } else {
        this.toastService.showToast('Profile unable to remove!');
      }
    })
  }

  fileSelected(event) {
    const file = event.target.files ? event.target.files[0] : '';
    this.selectedFile = file;

    let size = event.target.files[0].size;
    this.toastService.showToast(`profile picture will take time to load since the upload file size is ${this.formatBytes(size, 2)}`);

    const formData: FormData = new FormData();
    let payload: any = {
      "documentName": "Profile",
      "documentType": 1,
      "fileType": file.type,
      "fileName": file.name,
      "customerId": this.customerId
    };
    formData.append('file', file);
    formData.append('data', JSON.stringify(payload));

    this.uploadFile(formData);
  }


  formatBytes(bytes, decimals) {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  pickImage(sourceType) {
    const options: CameraOptions = {
      quality: 70,
      sourceType: sourceType,
      destinationType: this.camera.DestinationType.FILE_URI,
      mediaType: this.camera.MediaType.ALLMEDIA,
      correctOrientation: true,
      targetHeight: 350,
      targetWidth: 350,
      cameraDirection: 1
    };

    this.camera.getPicture(options).then((imageData) => {
      console.log(imageData);
      this.crop.crop(imageData, {
        quality: 50, targetHeight: 350,
        targetWidth: 350,
      })
        .then(newImage => {
          console.log('new image path is: ', newImage);
          this.file.resolveLocalFilesystemUrl(newImage).then((entry: FileEntry) => {
            entry.file(file => {
              console.log(file);
              this.filePath.resolveNativePath(newImage)
                .then(filePath => {
                  console.log("Resolve file", file);
                  this.readFile(file, filePath);
                  console.log("filePath native::", filePath);
                }).catch(e => console.log(e));
            });
          });
        }, error => {
          console.error('Error cropping image', error);
        });
    }, (err) => {
      // Handle error
      this.toastService.showToast('Cordova is not available');
    });
  }


  readFile(file: any, targetUrl: any) {
    const reader = new FileReader();
    reader.onload = () => {
      const blob = new Blob([reader.result], {
        type: file.type
      });
      console.log("this.read::", file);
      let filename = targetUrl.split("/").pop();
      const formData = new FormData();

      let payload: any = {
        "documentName": "Profile",
        "documentType": 1,
        "fileType": file.type,
        "fileName": file.name,
        "customerId": this.customerId
      };

      formData.append('file', blob, filename);
      formData.append('data', JSON.stringify(payload));
      this.uploadFile(formData);
    };
    reader.readAsArrayBuffer(file);
  };

  uploadFile(formData: any) {
    this.showLoader = true;
    this.api.uploadProfilePicture(formData)
      .subscribe((event: any) => {
        if (event.type === HttpEventType.UploadProgress) {
          this.progress = Math.round(100 * event.loaded / event.total);
        } else if (event instanceof HttpResponse) {
          let avatarUrl = event.body.fileUrl;
          this.image = avatarUrl;
          this.showLoader = false;
          localStorage.setItem('avatarUrl', avatarUrl);
          this.dataService.shareAvatarUrl(avatarUrl);
          this.toastService.showToast("File Uploaded Successfully!");
        }
        this.cdr.markForCheck();
      }, (err: any) => {
        this.progress = 0;
        this.showLoader = false;
        this.selectedFile = null;
      });
  }

  goToFeedback() {
    this.router.navigateByUrl("/feedback", { state: { goBack: true } });
  }

  previous() {
    this.router.navigate(['/dashboard'])
  }
}


